<template>
  <div class="undiagnosed" id="diagnosedPage">
    <el-row>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">PACS</el-breadcrumb-item>
        <el-breadcrumb-item>已诊列表</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>

    <el-row class="operationNew">
      <el-col v-bind:class="{ active: isActive1 }" :span="2"><a @click="turnBlue" id="isActive1" href="javascript:;">全部</a></el-col>
      <el-col v-bind:class="{ active: isActive4 }" :span="3" class="timepicker"><a @click="" id="isActive4" href="javascript:;"><el-date-picker
        v-model="dateValue"
        align="right"
        type="date"
        @change="selectDate"
        placeholder="选择日期"
        :picker-options="pickerOptions">
      </el-date-picker></a></el-col>
      <el-col :span="6">&nbsp;</el-col>
      <el-col :span="4"><el-button type="primary" @click="batchPdfGen">批量生成报告<i class="el-icon-printer el-icon--right"></i></el-button></el-col>
      <div style="text-align: center;">
        <el-col :span="2"><a href="javascript:;" v-on:click="refresh"><i class="icon iconfont icon-shuaxin1"></i>刷新</a>
          <a style="display:none;" id="downloadzip" href="" download="Re.zip">Download file</a>
        </el-col>
        <el-col :span="5"><el-input
          v-model="inputVal"
          placeholder="输入患者姓名/手机号/身份证号 可模糊检索">
        </el-input></el-col>
        <el-col :span="2"><el-button type="primary" @click="searchBtn" icon="el-icon-search">搜索</el-button></el-col>
      </div>
    </el-row>

    <el-row class="dataZone">
      <template>
        <el-table
          ref="multipleTable"
          :data="tableData"
          stripe
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
<!--          <el-table-column-->
<!--            prop="patient_id"-->
<!--            label="患者编号"-->
<!--            >-->
<!--          </el-table-column>-->
          <el-table-column
            prop="patient_name"
            label="姓名"
            >
          </el-table-column>
          <el-table-column
            prop="patient_gender"
            label="性别"
            :formatter="formatSex">
          </el-table-column>
          <el-table-column
            prop="patient_age"
            label="年龄"
           >
          </el-table-column>
          <el-table-column
            prop="patient_tel"
            label="手机号"
            >
          </el-table-column>
          <el-table-column
            prop="apply_time"
            label="录入时间"
          :formatter="formatTime">
          </el-table-column>
          <el-table-column
            prop="status"
            label="状态"
            :formatter="formatStatus">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-tooltip content="查看报告" placement="left">
              <el-button @click="handleView(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-view" circle></el-button>
              </el-tooltip>

              <el-tooltip content="编辑" placement="right">
              <el-button @click="handleEdit(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-edit" circle></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </el-row>
    <el-row class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5,10]"
        :page-size="page_size"
        :pager-count="7"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </el-row>
    <!--编辑界面-->
    <el-dialog title="编辑" :visible.sync="editFormVisible" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="姓名" prop="patient_name" style="width: 25%">
          <el-input v-model="editForm.patient_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="patient_tel">
          <el-input type="text" placeholder="手机号" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="editForm.patient_tel" style="width: 25%"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="patient_age">
          <el-input-number v-model="editForm.patient_age" oninput="value=value.replace(/[^\d]/g,'')" :min="0" :max="200" style="width: 25%"></el-input-number>
        </el-form-item>
        <el-form-item label="身份证号" prop="ID_number">
          <el-input type="text" placeholder="身份证"  oninput="value=value.replace(/[\W]/g,'')" maxlength="18" show-word-limit v-model="editForm.ID_number" style="width: 25%"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio-group v-model="editForm.patient_gender" :onclick="handlePregnant()">
            <el-radio class="radio" :label="'0'">男</el-radio>
            <el-radio class="radio" :label="'1'">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="既往史">
          <template>
            <el-radio-group v-model="radio" :onchange="editjws()">
              <el-radio :label="3">有</el-radio>
              <el-radio :label="6">无</el-radio>
            </el-radio-group>
          </template>

          <br/>
          <el-form-item v-if="editFormjwsVisible">
            <el-checkbox v-model="editFormJwsForm.DM_year">糖尿病</el-checkbox>
            <el-checkbox v-model="editFormJwsForm.HTN_year">高血压</el-checkbox>
            <el-checkbox v-model="editFormJwsForm.HPL_year">高血脂</el-checkbox>
<!--            <span>糖尿病:</span><el-input-number v-model="editFormJwsForm.DM_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
<!--            <span>高血压:</span><el-input-number v-model="editFormJwsForm.HTN_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--            <br/>-->
<!--            <span>高血脂:</span><el-input-number v-model="editFormJwsForm.HPL_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
            <span>&nbsp;&nbsp;&nbsp;妊娠:</span><el-input-number v-bind:disabled="pregnant_seen" v-model="editFormJwsForm.pregnant_weeks" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>周
            <br/>
            <span>其他:&nbsp;&nbsp;&nbsp;</span><el-input size="mini" style="width: 410px"
                                                        placeholder="请输入内容(20个字符以内)"
                                                        maxlength="20"
                                                        v-model="editFormJwsForm.other"
                                                        clearable>
          </el-input>
            <br/>
            <span>屈光度:</span>
            左眼:<template>
            <el-select v-model="editFormLeftDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 160px">
              <el-option
                v-for="item in editFormLeftDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
            &nbsp;&nbsp;&nbsp;&nbsp;右眼:<template>
            <el-select v-model="editFormRightDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 160px">
              <el-option
                v-for="item in editFormLeftDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
          </el-form-item>
        </el-form-item>
        <br/>
        <el-form-item label="地址" prop="address">
          <el-input type="textarea" v-model="editForm.address"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false">取消</el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
      </div>
    </el-dialog>

    <!--历史诊断报告-->
    <!--此滚动页面的高度在mounted函数直接赋值-->
    <el-carousel :interval="1000000"  height="800px" v-if="reportFormVisible"
                 style="width: 800px;position: fixed;z-index: 99;left: 30%;top: 1%;bottom:1%"
                 :loop="false"
                  @change="changeCarousel">
      <el-carousel-item v-for="(item,i) in this.reportForm.diagnose" :key="item.id">
        <el-row style="text-align: center;font-weight: bold;font-size: x-large;margin-top: 10px">
          <el-col :span="5">&nbsp;</el-col>
          <el-col :span="14" id="report">诊断报告<span style="font-weight: unset;font-size: small;">(序号:{{i+1}}/{{reportForm.diagnose.length}})</span></el-col>
          <el-col :span="5" style="text-align: right">
            <el-tooltip content="关闭" placement="left">
            <a href="javascript:;" @click="closeReport"><i class="el-icon-circle-close"></i></a>
            </el-tooltip>
            &nbsp;
<!--            <el-switch-->
<!--            v-model="value"-->
<!--            active-color="#13ce66"-->
<!--            inactive-color="#ff4949"-->
<!--            @change="closeReport">-->
<!--          </el-switch>-->
<!--          <span style="font-weight: unset;font-size: small;">关闭</span>-->
          </el-col>
        </el-row>
        <el-row style="margin-top: 20px">
          <el-col :span="1">&nbsp;</el-col>
          <el-col :span="5">姓名:{{reportForm.patient_name}}</el-col>
          <el-col :span="5" v-if="reportForm.patient_gender==='0'">性别:男</el-col>
          <el-col :span="5" v-else>性别:女</el-col>
          <el-col :span="6">年龄:{{reportForm.patient_age}}</el-col>
          <el-col :span="7">诊断日期:{{reportForm.diagnose[i]["diagnose_time"]}}</el-col>
        </el-row>

        <el-row style="margin-top: 20px">
          <el-col :span="12" style="text-align: center">OD(图像质量:{{reportForm.diagnose[i]["iqa_score_right"]}})</el-col>
          <el-col :span="12" style="text-align: center">OS(图像质量:{{reportForm.diagnose[i]["iqa_score_left"]}})</el-col>
        </el-row>

        <el-row style="margin-top: 30px">
<!--          <el-col :span="12"><span>{{picList[i]['data'][0]}}</span></el-col>-->
          <el-col :span="12" style="text-align: center">
            <div class="demo-image__preview">
              <el-image
                id="right_origin_src"
                style="height: 250px;"
                :src="right_src"
                :preview-src-list="right_srcList">
              </el-image>
            </div>
          </el-col>
          <el-col :span="12" style="text-align: center">
            <div class="demo-image__preview">
              <el-image
                id="left_origin_src"
                style="height: 250px;"
                :src="left_src"
                :preview-src-list="left_srcList">
              </el-image>
            </div>
          </el-col>
        </el-row>

<!--        <el-row style="margin-top: 20px;font-weight: bold;font-size: large">-->
<!--          <el-col :span="1">&nbsp;</el-col>-->
<!--          <el-col :span="4">影像发现</el-col>-->
<!--          <el-col :span="19"></el-col>-->
<!--        </el-row>-->

<!--        <el-row style="margin-top: 10px">-->
<!--          <el-col :span="1">&nbsp;</el-col>-->
<!--          <el-col :span="4">类别</el-col>-->
<!--          <el-col :span="6">数量(个)</el-col>-->
<!--          <el-col :span="6">总面积mm<sup>2</sup></el-col>-->
<!--          <el-col :span="6">最大面积mm<sup>2</sup></el-col>-->
<!--          <el-col :span="1"></el-col>-->
<!--        </el-row>-->

<!--        <el-row style="margin-top: 10px">-->
<!--          <el-col :span="1">&nbsp;</el-col>-->
<!--          <el-col :span="4">出血</el-col>-->
<!--          <el-col :span="6" id="bleeding_amount">{{oozed_bleeding[i]["bleeding_amount"]}}</el-col>-->
<!--          <el-col :span="6" id="bleeding_area">{{oozed_bleeding[i]["bleeding_area"]}}</el-col>-->
<!--          <el-col :span="6" id="max_bleeding_area">{{oozed_bleeding[i]["max_bleeding_area"]}}</el-col>-->
<!--          <el-col :span="1"></el-col>-->
<!--        </el-row>-->

<!--        <el-row style="margin-top: 10px">-->
<!--          <el-col :span="1">&nbsp;</el-col>-->
<!--          <el-col :span="4">渗出</el-col>-->
<!--          <el-col :span="6" id="oozed_amount">{{oozed_bleeding[i]["oozed_amount"]}}</el-col>-->
<!--          <el-col :span="6" id="oozed_area">{{oozed_bleeding[i]["oozed_area"]}}</el-col>-->
<!--          <el-col :span="6" id="max_oozed_area">{{oozed_bleeding[i]["max_oozed_area"]}}</el-col>-->
<!--          <el-col :span="1"></el-col>-->
<!--        </el-row>-->

        <el-row style="margin-top: 40px;font-weight: bold;font-size: large">
          <el-col :span="1">&nbsp;</el-col>
          <el-col :span="4">诊断</el-col>
          <el-col :span="19"></el-col>
        </el-row>


        <el-row style="margin-top: 10px">
          <el-col :span="1">&nbsp;</el-col>
          <el-col :span="21" id="right_diagnose">右眼:&nbsp;&nbsp;{{reportForm.diagnose[i]['right_diagnose']}}</el-col>
          <el-col :span="2"></el-col>
        </el-row>

        <el-row style="margin-top: 10px">
          <el-col :span="1">&nbsp;</el-col>
          <el-col :span="21" id="left_diagnose">左眼:&nbsp;&nbsp;{{reportForm.diagnose[i]['left_diagnose']}}</el-col>
          <el-col :span="2"></el-col>
        </el-row>

        <el-row style="margin-top: 20px;font-weight: bold;font-size: large">
          <el-col :span="1">&nbsp;</el-col>
          <el-col :span="23">建议:</el-col>
        </el-row>

        <el-row style="margin-top: 10px;font-size: large" id="diagnose_advice">
          <el-col :span="1">&nbsp;</el-col>
          <el-col :span="23" v-if="reportForm.diagnose[i]['review_time']">请于{{reportForm.diagnose[i]["review_time"]}}前复诊</el-col>
          <el-col :span="23" v-else>{{reportForm.diagnose[i]["diagnose_advice"]}}</el-col>

        </el-row>


        <el-row style="margin-top: 140px;font-size: large">

          <el-col :span="24" style="text-align: center">
            <div class="button_area">
              <!--<div><el-button @click="openFullScreen">完成阅片</el-button></div>-->
              <div><el-button round @click="createReport">生成报告</el-button></div>
              <div><el-button round @click="clickCreateImgBtn">生成照片</el-button></div>
            </div>

          </el-col>
        </el-row>

      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
  import axios from 'axios'
  import {editPatientBasicInfo, get_patient_info, createImg} from '../../api/api';
  import cookie from "../../static/js/cookie";
  import url from "../../static/js/hostUrl";

  let host = url.url;
  export default {
    inject: ['reload'],
    name:'undiagnosed',
    created:function(){
      this.get_patients(1,10)
    },
    methods:{
      /*组件重新加载*/
      reloadFun () {
        this.reload()
      },
      /*刷新*/
      refresh:function () {
        this.reloadFun();
        let p = 1;
        this.currentPage = 1;
        let page_size = this.page_size;
        let search  = this.inputVal;
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("total");
        }
      },
      /*控制每页多少数量*/
      handleSizeChange( val) {
        let page_size = val;
        this.page_size = page_size;
        this.currentPage = 1;
        let p = this.currentPage;
        let search = this.inputVal;
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("page_size total");
        }
      },
      /*控制第几页*/
      handleCurrentChange(val) {
        let p  = val;
        let page_size = this.page_size;
        let search = this.inputVal;
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("pagination total");
        }
      },

      turnBlue:function(e){
        let thisActive = e.currentTarget.id;
        if(thisActive === "isActive1"){
          console.log('isActive1');
          this.isActive1 = true;
          this.isActive2 = false;
          this.isActive3 = false;
          this.isActive4 = false;
          this.otherDate = false;
          this.dateValue = '';
          this.refresh();
        }
      },

      get_patients:function (p,size,apply_time,search) {
        let para = {};
        para['hospital_id'] = cookie.getCookie('hospital_id');
        para['p'] = p;
        para['page_size'] = size;
        para['ordering'] = '-apply_time';
        para['diagnose_record'] = 1;

        get_patient_info(para).then(response=>{
          console.log('response',response);
          this.tableData = response.results;
          this.totalNum = response.count;
        })
          .catch(error=>{
            console.log((error))
          })
      },
      get_patients_by_rules:function (p,size,apply_time,search) {
        let para = {};
        para['hospital_id'] = cookie.getCookie('hospital_id');
        para['p'] = p;
        para['page_size'] = size;
        para['apply_time'] = apply_time;
        para['search'] = search;
        para['ordering'] = '-apply_time';
        para['diagnose_record'] = 1;
        get_patient_info(para).then(response=>{
          console.log('response',response);
          this.tableData = response.results;
          this.totalNum = response.count;
        })
        // let hospital_id = cookie.getCookie('hospital_id');
        // axios.get(host+"/patients/?p="+p+"&page_size="+size+
        //   "&ordering=-apply_time"+"&apply_time="+apply_time+"&hospital_id="+hospital_id+"&search="+search+"&diagnose_record=1")
        //   .then(response=>{
        //     console.log((response))
        //     this.tableData = response.data.results
        //     this.totalNum = response.data.count
        //   })
        //   .catch(error=>{
        //     console.log((error))
        //   })
      },
      selectDate:function () {
        this.isActive1=false;
        this.isActive4=true;
        this.otherDate = true;
        let seperator1 = "-";
        let date = new Date(this.dateValue);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let selectdate = year + seperator1 + month + seperator1 + strDate;
        this.dateValue  = selectdate;
        this.currentPage = 1;
        let search = this.inputVal;
        console.log(this.dateValue);
        console.log(search);
        this.get_patients_by_rules(1,this.page_size,selectdate,search);
      },
      searchBtn:function () {
        console.log('searchBtn');
        this.currentPage = 1;
        this.get_patients_by_rules(1,this.page_size,this.dateValue,this.inputVal);
      },
      //性别显示转换
      formatSex: function (row) {
        return row.patient_gender === "0" ? '男' : row.patient_gender === "1" ? '女' : '未知';
      },
      //状态显示转换
      formatStatus: function (row) {
        return row.status === "0" ? '已诊断' : row.status === "2" ? '已诊断' : '已诊断';
      },
      //时间显示转化
      formatTime: function (row) {
        let lastOne = row.diagnose.length-1;
        let apply_time = row.diagnose[lastOne].apply_time;
        if(apply_time){
          return apply_time
        }else{
          return  row.diagnose[lastOne-1].apply_time
        }

      },
      //显示编辑界面
      handleEdit: function (index, row) {
        let historyDisease = row['historyDisease'];
        console.log(historyDisease);
        this.editForm = Object.assign({}, row);
        this.editForm.isFront=true;  //数据是前端传输过来
        if(historyDisease[0]){
          console.log('有既往史');
          this.editFormVisible = true;

          if(historyDisease[0]['DM_year']===1||historyDisease[0]['DM_year']===true){
            historyDisease[0]['DM_year']=true
          }
          if(historyDisease[0]['HPL_year']===1||historyDisease[0]['HPL_year']===true){
            historyDisease[0]['HPL_year']=true
          }
          if(historyDisease[0]['HTN_year']===1||historyDisease[0]['HTN_year']===true){
            historyDisease[0]['HTN_year']=true
          }
          if(historyDisease[0]['pregnant_weeks']===0){
            historyDisease[0]['pregnant_weeks']=''
          }

          this.editFormJwsForm = historyDisease[0];
          this.editFormLeftDiopterValue = historyDisease[0]['left_diopter'];
          this.editFormRightDiopterValue=historyDisease[0]['right_diopter'];
          this.editForm.jwsExist=1;  //展示既往史具体内容
          this.editFormjwsVisible=true; //展示编辑界面
          this.radio=3; //点亮‘有’字
        }else{
          console.log('无既往史');
          this.editFormVisible = true;
          this.editForm.jwsExist=0; //不展示既往史具体内容
          this.editFormjwsVisible=false; //不展示编辑界面
          this.radio=6;//点亮‘无’字
        }
      },

      editjws:function(){
        let radio = this.radio;
        console.log(radio);
        if(radio===3){
          this.editForm.jwsExist=1;
          this.editFormjwsVisible=true;
        }else if(radio===6){
          this.editFormjwsVisible=false;
          this.editFormJwsForm={};
          this.editFormRightDiopterValue='';
          this.editFormLeftDiopterValue='';
