      nominateZip:function(){
    var a = document.getElementById("downloadzip");
    var time = this.getNowFormatDate();
    a.setAttribute("download",time+".zip");
  },
      getNowFormatDate:function() {
        let date = new Date();
        let seperator1 = "";//设置成自己想要的日期格式 年/月/日
        let seperator2 = "_";//设置成自己想要的时间格式 时:分:秒
        let month = date.getMonth() + 1;//月
        let strDate = date.getDate();//日
    if (month >= 1 && month <= 9)
    {
      month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9)
    {
      strDate = "0" + strDate;
    }
        let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
      + "/" + date.getHours() + seperator2 + date.getMinutes()
      + seperator2 + date.getSeconds();
    return currentdate;
  }

    },
    data() {
      let validateTel = (rule, value, callback) => {
        let telLength = value.length;
        if (telLength !==11 ) {
          callback(new Error('手机号为11位'));
        } else {
          callback();
        }
      };

      let validateIDnumber = (rule, value, callback) => {
        if(value){
          let telLength = value.length;
          if (telLength !==18 ) {
            callback(new Error('中国大陆身份证号为18位'));
          } else {
            callback();
          }
        }else{
          callback();
        }
      };

      let validateName = (rule, value, callback) => {
        let nameLength = value.length;
        let reg = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
        if (nameLength >10 ) {
          callback(new Error('姓名长度需要小于10个字'));
        }else if(!reg.test(value)){
          callback(new Error('禁止输入空格等特殊字符'));
        }
        else {
          callback();
        }

      };

      let validateAddress = (rule, value, callback) => {
        let nameLength = value.length;
        if (nameLength >30 ) {
          callback(new Error('地址长度需要小于30个字'));
        } else {
          callback();
        }
      };

      let validateAge = (rule, value, callback) => {
        if (value<1) {
          callback(new Error('请填写正确年龄'));
        } else {
          callback();
        }
      };
      return {
        carouselIndex:0,
        pregnant_seen:false,
        oozed_bleeding:[],
        picList:[],
        tableData: [],
        multipleSelection: [],
        dateValue:'',
        isActive1:true,
