        isActive3:false,
        isActive4:false,
        currentPage: 1,
        inputVal:'',
        value: true,
        page_size:10,
        totalNum:30,
        otherDate:false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        editFormVisible: false,//编辑界面是否显示
        reportFormVisible: false,//历史诊断报告界面是否显示
        editLoading: false,
        editFormRules: {
          patient_name: [
            { required: true, message: '请输入姓名', trigger: 'change' },
            { validator: validateName, trigger: 'change' },
          ],
          patient_tel: [
            { required: true, message: '请输入手机号', trigger: 'change' },
            { validator: validateTel, trigger: 'change' },
          ],
          patient_age: [
            { required: true, message: '请输入正确的年龄', trigger: 'blur' },
            { validator: validateAge, trigger: 'blur' },
          ],
          ID_number: [
            { validator: validateIDnumber, trigger: 'change' },
            {
              pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
              message: '证件号码格式有误！',
              trigger: 'change'
            }
          ],
          address: [
            { validator: validateAddress, trigger: 'change' },
          ],
        },
        //编辑界面数据
        editForm: {
          patient_id: 0,
          patient_name: '测试',
          patient_gender: '0',
          patient_age: 0,
          patient_tel:18888888888,
          ID_number:'',
