  /*  color: #FFFFFF;*/
  /*}*/

  .pagination{
    margin-top: 20px;
    text-align: right;
  }

  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 140px;
  }

  .timepicker >>> i{
    color: dodgerblue;
    font-size: x-large;
  }
  .timepicker >>> input {
    border: none;
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: dodgerblue;
  }

  .timepicker >>>input::-moz-placeholder{
    color: blue;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #efefef;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #efefef;
  }
</style>




