<template>
    <div class="undiagnosed">
      <el-row>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">PACS</el-breadcrumb-item>
          <el-breadcrumb-item>未诊列表</el-breadcrumb-item>
        </el-breadcrumb>
      </el-row>

      <el-row class="operationNew">
        <el-col v-bind:class="{ active: isActive1 }" :span="2"><a @click="turnBlue" id="isActive1" href="javascript:;">全部</a></el-col>
        <el-col v-bind:class="{ active: isActive4 }" :span="3" class="timepicker"><a @click="" id="isActive4" href="javascript:;"><el-date-picker
          v-model="dateValue"
          align="right"
          type="date"
          @change="selectDate"
          placeholder="选择日期"
          :picker-options="pickerOptions">
        </el-date-picker></a></el-col>
        <el-col :span="6">&nbsp;</el-col>
        <el-col :span="4"><el-button type="primary" @click="handleAdd">添加患者<i class="el-icon-upload el-icon--right"></i></el-button></el-col>
        <div style="text-align: center">
        <el-col :span="2"><a href="javascript:;" v-on:click="refresh"><i class="icon iconfont icon-shuaxin1"></i>刷新</a></el-col>
        <el-col :span="5"><el-input
          v-model="inputVal"
          placeholder="输入患者姓名/手机号/身份证号 可模糊检索"
          >
        </el-input></el-col>
        <el-col :span="2"><el-button type="primary" @click="searchBtn" icon="el-icon-search">搜索</el-button></el-col>
        </div>
      </el-row>

      <!--上传照片-->
      <el-row>
        <form action="" method="POST" id="jvForm" enctype="multipart/form-data" style="width:auto;">
          <input type="file" name= "patient_img" accept="image/*"  multiple="multiple"  id="fileToUpload"  @change="uploadPic" style="display:none;">
        </form>
      </el-row>

      <el-row class="dataZone">
        <template>
          <el-table
            :data="tableData"
            stripe
            style="width: 100%">
<!--            <el-table-column-->
<!--              prop="patient_id"-->
<!--              label="患者编号"-->
<!--              width="220"-->
<!--              >-->
<!--            </el-table-column>-->
            <el-table-column
              prop="patient_name"
              label="姓名"
              >
            </el-table-column>
            <el-table-column
              prop="patient_gender"
              label="性别"

              :formatter="formatSex">
            </el-table-column>
            <el-table-column
              prop="patient_age"
              label="年龄"
              >
            </el-table-column>
            <el-table-column
              prop="patient_tel"
              label="手机号"
              >
            </el-table-column>
            <el-table-column
              prop="apply_time"
              label="录入时间"
              >
            </el-table-column>
            <el-table-column
              prop="status"
              label="状态"
              :formatter="formatStatus">
            </el-table-column>
            <el-table-column

              label="操作">
              <template slot-scope="scope">
                <el-tooltip content="诊断" placement="left">
                <el-button   @click="clickToDiagnose(scope.$index, scope.row)" type="primary" size="small" v-if="formatButton(scope.row)" icon="el-icon-first-aid-kit" circle></el-button>
                </el-tooltip>

                <el-tooltip content="删除" placement="left">
                <el-button @click="handleDel(scope.$index, scope.row)" type="danger" size="small" icon="el-icon-delete" circle v-if="!formatButton(scope.row)"></el-button>
                </el-tooltip>

                <el-tooltip content="编辑" placement="right">
                <el-button @click="handleEdit(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-edit" circle></el-button>
                </el-tooltip>

                <el-tooltip content="上传照片" placement="right">
                  <el-button @click="uploadPicture(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-upload2" circle></el-button>
                </el-tooltip>

              </template>
            </el-table-column>
          </el-table>
        </template>
      </el-row>
     <el-row class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5,10]"
        :page-size="page_size"
        :pager-count="7"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
     </el-row>
      <!--新增界面-->
      <el-dialog title="添加患者" :visible.sync="addFormVisible" v-model="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label="姓名"  prop="patient_name" style="width: 25%">
            <el-input  v-model="addForm.patient_name"  @blur="checkIsDiagnoseAgain" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="patient_tel">
            <el-input type="text" placeholder="手机号" @blur="checkIsDiagnoseAgain" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="addForm.patient_tel" style="width: 25%"></el-input>
          </el-form-item>
          <el-form-item label="年龄" prop="patient_age">
            <el-input-number v-model="addForm.patient_age"  :min="0" :max="200" style="width: 25%"></el-input-number>
          </el-form-item>
          <el-form-item label="身份证号" prop="ID_number">
            <el-input type="text" placeholder="身份证"  oninput="value=value.replace(/[\W]/g,'')" maxlength="18" show-word-limit v-model="addForm.ID_number" style="width: 25%"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="addForm.patient_gender" :onclick="disabledPregnant()">
              <el-radio class="radio" :label="0" v-model="addForm.genderRadio">男</el-radio>
              <el-radio class="radio" :label="1" v-model="addForm.genderRadio">女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="既往史" prop="jwsExist">
            <el-radio-group v-model="addForm.jwsExist"
            :onclick="jwsClick()">
              <el-radio class="radio" :label="1" v-model="addForm.jwsRadio">有</el-radio>
              <el-radio class="radio" :label="0" v-model="addForm.jwsRadio">无</el-radio>
            </el-radio-group>
            <br/>
            <el-form-item v-if="addForm.jwsVisible">
            <el-checkbox v-model="jwsForm.DM_year">糖尿病</el-checkbox>
            <el-checkbox v-model="jwsForm.HTN_year">高血压</el-checkbox>
            <el-checkbox v-model="jwsForm.HPL_year">高血脂</el-checkbox>
<!--            <span>糖尿病:</span><el-input-number v-model="jwsForm.DM_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
<!--            <span>高血压:</span><el-input-number v-model="jwsForm.HTN_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--            <br/>-->
<!--            <span>高血脂:</span><el-input-number v-model="jwsForm.HPL_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->

            <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;妊娠:</span><el-input-number v-bind:disabled="pregnant_seen" v-model="jwsForm.pregnant_weeks" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>周
              <br/>
            <span>其他:&nbsp;&nbsp;&nbsp;</span><el-input  size="mini" style="width: 434px"
            placeholder="请输入内容(20个字符以内)"
            maxlength="20"
            v-model="jwsForm.other"
            clearable>
          </el-input>
            <br/>
            <span>屈光度:</span>
            左眼:<template>
            <el-select v-model="leftDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 160px">
              <el-option
                v-for="item in leftDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;右眼:<template>
            <el-select v-model="rightDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 160px">
              <el-option
                v-for="item in rightDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
          </el-form-item>
          </el-form-item>
            <br/>
          <el-form-item label="地址" prop="address">
            <el-input type="textarea" v-model="addForm.address"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>
        <!--编辑界面-->
        <el-dialog title="编辑" :visible.sync="editFormVisible" v-model="editFormVisible" :close-on-click-modal="false">
          <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
            <el-form-item label="姓名" prop="patient_name" style="width: 25%">
              <el-input v-model="editForm.patient_name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="patient_tel">
              <el-input type="text" placeholder="手机号" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="editForm.patient_tel" style="width: 25%"></el-input>
            </el-form-item>
            <el-form-item label="年龄" prop="patient_age">
              <el-input-number v-model="editForm.patient_age"  :min="0" :max="200" style="width: 25%"></el-input-number>
            </el-form-item>
            <el-form-item label="身份证号" prop="ID_number">
              <el-input type="text" placeholder="身份证"  oninput="value=value.replace(/[\W]/g,'')" maxlength="18" show-word-limit v-model="editForm.ID_number" style="width: 25%"></el-input>
            </el-form-item>
            <el-form-item label="性别">
              <el-radio-group v-model="editForm.patient_gender" :onchange="handlePregnant()">
                <el-radio class="radio" :label="'0'">男</el-radio>
                <el-radio class="radio" :label="'1'">女</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="既往史">
              <template>
                <el-radio-group v-model="radio" :onchange="editjws()">
                  <el-radio :label="3">有</el-radio>
                  <el-radio :label="6">无</el-radio>
                </el-radio-group>
              </template>

              <br/>
              <el-form-item v-if="editFormjwsVisible">
                <el-checkbox v-model="editFormJwsForm.DM_year">糖尿病</el-checkbox>
                <el-checkbox v-model="editFormJwsForm.HTN_year">高血压</el-checkbox>
                <el-checkbox v-model="editFormJwsForm.HPL_year">高血脂</el-checkbox>
<!--                <span>糖尿病:</span><el-input-number v-model="editFormJwsForm.DM_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
<!--                <span>高血压:</span><el-input-number v-model="editFormJwsForm.HTN_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
<!--                <br/>-->
<!--                <span>高血脂:</span><el-input-number v-model="editFormJwsForm.HPL_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
                <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;妊娠:</span><el-input-number v-bind:disabled="pre_seen" v-model="editFormJwsForm.pregnant_weeks" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>周
                <br/>
                <span>其他:&nbsp;&nbsp;&nbsp;</span><el-input size="mini" style="width: 434px"
                                                            placeholder="请输入内容(20个字符以内)"
                                                            maxlength="20"
                                                            v-model="editFormJwsForm.other"
                                                            clearable>
              </el-input>
                <br/>
                <span>屈光度:</span>
                左眼:<template>
                <el-select v-model="editFormLeftDiopterValue"  size="mini" clearable  placeholder="请选择" style="width: 160px">
                  <el-option
                    v-for="item in editFormLeftDiopter"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </template>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;右眼:<template>
                <el-select v-model="editFormRightDiopterValue"  size="mini" clearable  placeholder="请选择" style="width: 160px">
                  <el-option
                    v-for="item in editFormLeftDiopter"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </template>
              </el-form-item>
            </el-form-item>
            <br/>
            <el-form-item label="地址" prop="address">
              <el-input type="textarea" v-model="editForm.address"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
          </div>
        </el-dialog>
    </div>
</template>

<script>
  import axios from 'axios'
  import {
    addPatient,
    checkPatient,
    clickDiagnoseButton,
    editPatientBasicInfo,
    get_patient_info,
    removePatient,
    check_expired
  } from '../../api/api';
  import url from "../../static/js/hostUrl";
  import cookie from "../../static/js/cookie";
  import "../../static/js/jquery.form";
  let host = url.url;
  export default {
    inject: ['reload'],
    name:'undiagnosed',
    created:function(){

      let name = cookie.getCookie('name');
      this.get_hospital_id(name);  /*ajax请求会被异步执行，即被放到最后处理*/

    },
    mounted:function(){
      let rightNavWidth = window.innerWidth-250;
      console.log(rightNavWidth);
      document.getElementById('rightNav').style.width = rightNavWidth+'px';
    },

    watch:{                           /*采用watch方法，监听到hospital_id变化时再执行get_patients方法*/
      hospital_id(newVal,oldVal){
        console.log('oldVal',oldVal);
        console.log('newVal',newVal);
        let page_info = this.$store.state.page_info;
        let flag = this.$store.state.flag;
        if(flag){this.get_patients(1,10);return false};

        if(page_info.page_size&&page_info.currentPage){
          this.page_size = page_info.page_size;
          this.currentPage = page_info.currentPage;
          this.get_patients(page_info.currentPage,page_info.page_size);
        }else{
          this.get_patients(1,10);
        }
      },
      immediate: true
    },

    methods:{
      /*组件重新加载*/
      reloadFun () {
        this.reload()
      },
      /*刷新*/
      refresh:function () {
        this.reloadFun();
        let p = 1;
        this.currentPage = 1;
        let page_size = this.page_size;
        let search  = this.inputVal;
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate",p,page_size);
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("total",p,page_size);
          console.log("this.currentPage",this.currentPage);
        }
      },
      /*向store.js传递page和size*/
      delieverPageAndSize:function(){
        let currentPage = this.currentPage;
        let page_size = this.page_size;
        let page_info = {};
        page_info['currentPage'] = currentPage;
        page_info['page_size'] = page_size;
        this.$store.commit('rememberPageAndSize',page_info)
      },

      /*控制每页多少数量*/
      handleSizeChange( val) {
        let flag = false;
        this.$store.commit('changeFlag',flag);
        let page_size = val;
        this.page_size = page_size;
        this.currentPage = 1;
        let p = this.currentPage;
        let search = this.inputVal;
        this.delieverPageAndSize();
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("page_size total");
        }
      },
      /*控制第几页*/
      handleCurrentChange(val) {
        let flag = false;
        this.$store.commit('changeFlag',flag);
        let p  = val;
        let page_size = this.page_size;
        let search = this.inputVal;
        console.log(p,page_size);
        this.delieverPageAndSize();
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("pagination total");
        }
      },

      turnBlue:function(e){
        let thisActive = e.currentTarget.id;
        if(thisActive === "isActive1"){
          console.log('isActive1');
          this.isActive1 = true;
          this.isActive4 = false;
          this.otherDate = false;
          this.dateValue = '';
          this.refresh();
        }
      },

      get_hospital_id:function (username) {
        axios.get(host+"/users/?username="+username)
          .then(response=>{
            let hospital_id = response.data[0]['hospital_id'];
            this.hospital_id = hospital_id;
            console.log('第一步从服务器获得了',this.hospital_id);
          })
          .catch(error=>{
            console.log((error))
          })

      },


      get_patients:function (p,size,apply_time,search) {
        let para = {};
        // let hospital_id = cookie.getCookie('hospital_id');
        // let name = cookie.getCookie('name');
        // if(!hospital_id){
        //   hospital_id = this.get_hospital_id(name);
        //   console.log('第二步获取hospital_id')
        // }
        let hospital_id = this.hospital_id;
        console.log('第二步获取hospital_id',hospital_id);
        para['hospital_id'] = hospital_id;
        para['p'] = p;
        para['page_size'] = size;
        para['apply_time'] = apply_time;
        para['search'] = search;
        para['ordering'] = '-apply_time';
        para['status'] = 0;

        get_patient_info(para).then(response=>{
            console.log('response',response);
            this.tableData = response.results;
            this.totalNum = response.count;
          })
          //诊断（如第二页最后一个患者时）完毕后，再返回第二页会出现错误，此处需要将页数减1
          .catch(error=>{
            console.log((error));
            p = p-1;
            axios.get(host+"/patients/?p="+p+"&page_size="+size+"&hospital_id="+hospital_id+"&ordering=-apply_time&status=0")
              .then(response=>{
                console.log('response',response);
                this.tableData = response.data.results;
                this.totalNum = response.data.count;
              })
          })
      },

      get_patients_by_rules:function (p,size,apply_time,search) {
        let para = {};
        para['hospital_id'] = cookie.getCookie('hospital_id');
        para['p'] = p;
        para['page_size'] = size;
        para['apply_time'] = apply_time;
        para['search'] = search;
        para['ordering'] = '-apply_time';
        para['status'] = 0;

        get_patient_info(para).then(response=>{
          console.log('response',response);
          this.tableData = response.results;
          this.totalNum = response.count;
        })
        // axios.get(host+"/patients/?p="+p+"&page_size="+size+
        //   "&ordering=-apply_time"+"&apply_time="+apply_time+"&hospital_id="+hospital_id+"&search="+search+"&status=0")
        //   .then(response=>{
        //     console.log((response));
        //     this.tableData = response.data.results;
        //     this.totalNum = response.data.count
        //   })
          .catch(error=>{
            console.log((error))
          })
      },
      selectDate:function () {
        this.isActive1=false;
        this.isActive4=true;
        this.otherDate = true;
        let seperator1 = "-";
        let date = new Date(this.dateValue);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let selectdate = year + seperator1 + month + seperator1 + strDate;
        this.dateValue  = selectdate;
        this.currentPage = 1;
        let search = this.inputVal;
        console.log(this.dateValue);
        console.log(search);
        this.get_patients_by_rules(1,this.page_size,selectdate,search);
      },
      searchBtn:function () {
        console.log('searchBtn');
        this.currentPage = 1;
        this.get_patients_by_rules(1,this.page_size,this.dateValue,this.inputVal);
      },
      //性别显示转换
      formatSex: function (row) {
        return row.patient_gender === "0" ? '男' : row.patient_gender === "1" ? '女' : '未知';
      },
      //状态显示转换
      formatStatus: function (row) {
        return row.status === "0" ? '未诊断' : row.status === "2" ? '已诊断' : '诊断中';
      },
      //诊断或删除按钮转换
      formatButton:function(row){
        return row.pic_upload === "1" ? true : row.pic_upload !== "0";
      },
      //显示编辑界面
      handleEdit: function (index, row) {
        let historyDisease = row['historyDisease'];
        console.log(historyDisease);
        this.editForm = Object.assign({}, row);
        this.editForm.isFront=true;  //数据是前端传输过来
        if(historyDisease[0]){
          console.log('有既往史');
          this.editFormVisible = true;

          if(historyDisease[0]['DM_year']===1||historyDisease[0]['DM_year']===true){
            historyDisease[0]['DM_year']=true
          }
          if(historyDisease[0]['HPL_year']===1||historyDisease[0]['HPL_year']===true){
            historyDisease[0]['HPL_year']=true
          }
          if(historyDisease[0]['HTN_year']===1||historyDisease[0]['HTN_year']===true){
            historyDisease[0]['HTN_year']=true
          }
          if(historyDisease[0]['pregnant_weeks']===0){
            historyDisease[0]['pregnant_weeks']=''
          }
          this.editFormJwsForm = historyDisease[0];


