      get_file_num :function () {
        let fileReturnValue = [];
        let fileNum = document.getElementById("fileToUpload").files.length;
        let files = document.getElementById("fileToUpload").files;
        for (let i=0;i<files.length;i++){
          console.log('1024',files[i].size);
          if(files[i].size/1048576>15){
            let fileSize = false;
            fileReturnValue[0]=fileSize;
            break
          }else{
            let fileSize = true;
            fileReturnValue[0]=fileSize;
          }
        }

        fileReturnValue.push(fileNum);

        return fileReturnValue;
      },
      uploadPic:function() {
        // check_expired().then(response=>{
        //   console.log('response',response);
        // });
        const loading = this.$loading({
          lock: true,
          // text: 'Loading',
          text: 'AI智能识别左右眼中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
        });
        setTimeout(() => {
          loading.setText('AI智能计算中');
        }, 3000);

        let num = this.get_file_num()[1];
        let fileSize = this.get_file_num()[0];
        if(num>2){
          this.$alert('单次上传照片数量不可超过两张', '温馨提示', {
            confirmButtonText: '确定',
            callback: action => {
              console.log(action)
            }
          });
          loading.close();
          return false;
        }else if(fileSize===false){
          this.$alert('单张照片大小不可超过10M', '温馨提示', {
            confirmButtonText: '确定',
            callback: action => {
              console.log(action)
            }
          });
          loading.close();
        }else {
          // 上传设置
          let options = {
            // 规定把请求发送到那个URL
            url: host+ "operation/web_upload_picture/",
            data:{'userName':cookie.getCookie('name'),
              'patient_id':this.upload_id,
              'diagnose_record':this.diagnose_record},
            // 请求方式
            type: "post",
            // 服务器响应的数据类型
            dataType:"json",
            cache: false,
            processData: false,   // 告诉jQuery不要处理数据
            contentType: false,   // 告诉jQuery不要设置类型
            // 请求成功时执行的回调函数
            // error:function (result, status, xhr) {
            //    alert("session 过期");
            // }

            success: function(result, status, xhr) {
              console.log('result',result);
              if(result===0){
                loading.close();
                this.$alert('上传失败,仅支持jpg,png,jpeg,bmp格式', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }else if(result===1){
                loading.close();
                this.$alert('上传失败', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }else if(result===2){
                loading.close();
                let flag = true;
                this.$store.commit('changeFlag',flag);
                this.refresh();
                this.$alert('上传成功', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }
              else{
                loading.close();
                this.$alert('上传失败', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }
            }.bind(this),
            error:function () {
              loading.close();

              this.$alert('上传失败', '温馨提示', {
                confirmButtonText: '确定',
                callback: action => {
                  console.log(action)
                }
              });
            }.bind(this)
          };
          $("#jvForm").ajaxSubmit(options);
        }
      },

    },


    data() {
      let validateTel = (rule, value, callback) => {
        let telLength = value.length;
        if (telLength !==11 ) {
          callback(new Error('手机号为11位'));
        } else {
          callback();
        }
      };

      let validateIDnumber = (rule, value, callback) => {
        if(value){
          let telLength = value.length;
          if (telLength !==18 ) {
            callback(new Error('中国大陆身份证号为18位'));
          } else {
            callback();
          }
        }else{
          callback();
        }
      };

      let validateName = (rule, value, callback) => {
        let nameLength = value.length;
        let reg = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
        if (nameLength >10 ) {
          callback(new Error('姓名长度需要小于10个字'));
        }else if(!reg.test(value)){
          callback(new Error('禁止输入空格等特殊字符'));
        }
        else {
          callback();
        }

      };

      let validateAddress = (rule, value, callback) => {
        if(!value){
          callback();
        }
        let nameLength = value.length;
        if (nameLength >30 ) {
          callback(new Error('地址长度需要小于30个字'));
        } else {
          callback();
        }
      };

      let validateAge = (rule, value, callback) => {
        if (value<1) {
          callback(new Error('请填写正确年龄'));
