        editFormRightDiopterValue: '',
      }
    }
  }

</script>

<style scoped>
.undiagnosed{
  text-align: left;
  margin-top: 20px;
}
  .timezone,.dataZone{
    margin-top: 20px;
  }
  .el-date,.el-icon-date,.icon-shuaxin1{
    color: dodgerblue;
  }

.operationNew{
  margin-top: 20px;
  text-align: center;
  line-height: 41px;
}
  a{
    text-decoration: none;
    color: dodgerblue;
  }

  .el-input__inner{
    border: dodgerblue 1px solid;
    color: dodgerblue;
  }

  .active{
    border: 1px solid dodgerblue;
    border-radius: 4%;
  }


  .pagination{
    margin-top: 20px;
    text-align: right;
  }

.el-date-editor.el-input, .el-date-editor.el-input__inner{
  width: 140px;
}

.timepicker >>> i{
  color: dodgerblue;
  font-size: x-large;
}
  .timepicker >>> input {
    border: none;
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: dodgerblue;
  }

.timepicker >>>input::-moz-placeholder{
  color: blue;
}

/*.el-loading-text{*/
/*  font-size: x-large;*/
/*}*/

@media screen and (min-width:600px) { /*大于600px小于1650px*/
  .undiagnosed {
    /*background-color: blue;*/
  }

}

@media screen and (min-width:1650px) { /*大于1650px小于1900px*/
  .undiagnosed {
    /*background-color: red;*/
  }
}

@media screen and (min-width:1900px) {  /*大于1900px*/
  .undiagnosed {
    /*background-color: yellow;*/
  }
}

</style>



