        </el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisibleA"
          v-model="inputValueA"
          ref="saveTagInputA"
          size="small"
          @keyup.enter.native="handleInputConfirmA"
          @blur="handleInputConfirmA"
        >
        </el-input>
         <el-tooltip  placement="right">
          <div slot="content">点击后可手动输入诊疗建议<br/>点击后也可从左侧建议栏选择建议</div>
        <el-button v-if="seen" class="button-new-tag"  size="small" @click="showInputA" >+ 添加建议</el-button>
         </el-tooltip>
        &nbsp;&nbsp;&nbsp;<span v-if="seen2">日期:{{value2}}</span>
      </span></el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 20px;" v-if="eyeSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16"><span>建议:{{dynamicTagsA[0]}}

        &nbsp;&nbsp;&nbsp;<span v-if="seen2">日期:</span>{{value2}}
      </span></el-col>
      <el-col :span="4">&nbsp;</el-col>
