          <!--<el-col :span="6"><el-button @click="backToUndiagnosed">完成并返回</el-button></el-col>-->
          <!--<el-col :span="6"><el-button @click="finishAndCreatePdf">完成并生成报告</el-button></el-col>-->
          <!--<el-col :span="6"><el-button @click="onlyBack">返回</el-button></el-col>-->
        <!--</el-row>-->

        <div class="button_area">
          <!--<div><el-button @click="openFullScreen">完成阅片</el-button></div>-->
          <div><el-button @click="clickCreateImgBtn">生成照片</el-button></div>
          <div><el-button @click="backToUndiagnosed">完成并返回</el-button></div>
          <div><el-button @click="finishAndCreatePdf">完成并生成报告</el-button></div>
          <div><el-button @click="onlyBack">返回</el-button></div>
        </div>
      </el-col>

      <el-col :span="4"></el-col>
    </el-row>

    <el-row style="margin-top: 20px;height: 40px;line-height: 40px" v-if="finishSeen">
      <el-col :span="4">&nbsp;</el-col>

      <el-col :span="16" style="text-align: left;height: 60px;line-height: 60px;border-top: 1px solid lightgray;
      border-bottom:1px solid lightgray;">
        <el-row>
          <el-col :span="8"><el-button @click="onlyBack">返回</el-button></el-col>
          <el-col :span="8"><el-button @click="createReport">生成报告</el-button></el-col>
          <el-col :span="8">&nbsp;</el-col>
        </el-row>
      </el-col>

      <el-col :span="4"></el-col>
    </el-row>

  </el-main>
    <el-dialog
      title="请输入随访时间"
      :visible.sync="dialogVisible"
      :show-close="false"
      width="30%">
      <div class="block">
        <el-date-picker
          v-model="value2"
          align="right"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择日期"
          :picker-options="pickerOptions">
        </el-date-picker>
      </div>
      <span slot="footer" class="dialog-footer">
<!--    <el-button @click="cancelSelectDate">取 消</el-button>-->
    <el-button type="primary" @click="confirmSelectDate">确 定</el-button>
  </span>
    </el-dialog>


    <el-dialog
      title="请输入医院名称"
      :visible.sync="uplevelVisible"
      width="30%">
      <div class="block">
        <el-form>
        <el-form-item label="医院名称">
          <el-input
            v-model="uplevelHospital"
            auto-complete="off"
            placeholder="请输入医院名称(限制字数20以内)"
            maxlength="20"
            >
          </el-input>
        </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="cancelInputHospital">取 消</el-button>
    <el-button type="primary" @click="confirmInputHospital">确 定</el-button>
  </span>
    </el-dialog>

<!--    <el-dialog title="请输入上级医院名称" :visible.sync="uplevelVisible">-->
<!--      <el-form-item label="医院名称"  style="width: 30%">-->
<!--        <el-input v-model="uplevelValue" auto-complete="off"></el-input>-->
<!--      </el-form-item>-->
<!--    </el-dialog>-->




    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <span>生成报告失败</span>
      <span slot="footer" class="dialog-footer">
<!--    <el-button @click="centerDialogVisible = false">取 消</el-button>-->
    <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
  </span>
