<script>
  import store from '@/vuex/store'
  import axios from 'axios'
  import url from "../../static/js/hostUrl";
  import cookie from "../../static/js/cookie";
  import {user_diagnose, createImg} from '../../api/api';
  let host = url.url;
  // window.onresize=function () {
  //   /*获取div高度，减去46px是因为头部组件高度46px*/
  //   let leftNavHeight = window.innerHeight-46;
  //   document.getElementById('aside_id').style.height = leftNavHeight+'px';
  // };

  export default {
    data() {
      return {
        jwsDisplayInfo:'无',
        warning:'',
        warningShow:false,
        hospital_name:'',
        patient_info:{},
        restaurants: [],
        AIdata:{},
        state: '',
        timeout:  null,
        suggestions:['定时随访','进一步就诊检查','正常,无需随访'],
        diseases:['高血压视网膜病变','糖尿病视网膜病变','豹纹状眼底','渗出',
                  '青光眼','年龄相关性视网膜病变'],
        fits: ['fill',],
        right_src: 'https://www.orbisbiotech.cn/media/noimg.jpg',
        left_src: 'https://www.orbisbiotech.cn/media/noimg.jpg',
        right_srcList: [],
        left_srcList: [],
        dynamicTagsR: [], /*右眼印象*/
        dynamicTagsL: [], /*左眼印象*/
        dynamicTagsA:[],  /*建议*/
        diaStrR:'',       /*完成阅片后展示的右眼印象*/
        diaStrL:'',       /*完成阅片后展示的左眼印象*/
        diaStrA:'',       /*完成阅片后展示的建议*/
        finishSeen:false, /*完成阅片前展示的按钮*/
        beforeSeen:true,  /*完成阅片后展示的按钮*/
        inputVisibleR: false,
        inputVisibleL: false,
        inputVisibleA: false,
        leftEye:false,
        rightEye:false,
        advice:false,
        inputValueR: '',
        inputValueL: '',
        inputValueA: '',
        seen:true,
        seen2:false,
        eyeSeen:false,
        diaSeen:true,
        activeColor1:'azure',
        activeColor2:'azure',
        dialogVisible: false,
        uplevelVisible:false,
        uplevelHospital:'',
        fullscreenLoading: false,
        centerDialogVisible: false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '明天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周后',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
        strengthen:false,
      };
    },
    methods: {
      /*关闭右眼标签*/
      handleCloseR(tag) {
        this.dynamicTagsR.splice(this.dynamicTagsR.indexOf(tag), 1);
      },
      /*显示添加的输入框*/
      showInputR() {
        this.activeColor2 = 'lightblue';
        this.inputVisibleR = true;
        this.rightEye = true;
        this.leftEye = false;
        this.$nextTick(_ => {
          this.$refs.saveTagInputR.$refs.input.focus();
        });
      },
      /*确定输入*/
      handleInputConfirmR() {

        this.activeColor2 = 'azure';
        let inputValueR = this.inputValueR;
        if (inputValueR) {
          this.dynamicTagsR.push(inputValueR);
        }
        this.inputVisibleR = false;
        this.inputValueR = '';
      },

      /*关闭左眼标签*/
      handleCloseL(tag) {
        this.dynamicTagsL.splice(this.dynamicTagsL.indexOf(tag), 1);
      },
      /*显示左眼输入框*/
      showInputL() {
        this.activeColor2 = 'lightblue';
        this.inputVisibleL = true;
        this.rightEye = false;
        this.leftEye = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputL.$refs.input.focus();
        });
      },
      /*确定输入*/
      handleInputConfirmL() {
        this.activeColor2 = 'azure';
        let inputValueL = this.inputValueL;
        if (inputValueL) {
          this.dynamicTagsL.push(inputValueL);
        }
        this.inputVisibleL = false;
        this.inputValueL = '';
      },

      /*关闭建议标签*/
      handleCloseA(tag) {
        this.dynamicTagsA.splice(this.dynamicTagsA.indexOf(tag), 1);
        this.seen = true;
        this.seen2 = false;
        this.value2='';
      },
      /*显示建议输入框*/
      showInputA() {
        this.leftEye=false;
        this.rightEye=false;
        this.activeColor1 = 'lightblue';
        this.inputVisibleA = true;
        this.advice = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputA.$refs.input.focus();
        });
      },
      /*确定输入建议*/
      handleInputConfirmA() {
        this.activeColor1='azure';
        let inputValueA = this.inputValueA;
        let dynamicTagsA = this.dynamicTagsA;
        if (inputValueA) {
          this.dynamicTagsA.push(inputValueA);
        }
        if(dynamicTagsA.length !== 0 ){
          this.seen = false;
        }
        this.inputVisibleA = false;
        this.inputValueA = '';
      },
      /*点击诊断栏处诊断自动添加为诊断的标签*/
      addDiagnose(index){
        if(this.leftEye){
          this.dynamicTagsL.push(index);
          let tem_dynamicTagsL = this.duplicateRemove(this.dynamicTagsL);  /*去重*/
          this.dynamicTagsL = tem_dynamicTagsL;
          this.handleInputConfirmL();
        }else if(this.rightEye){
          this.dynamicTagsR.push(index);
          let tem_dynamicTagsR = this.duplicateRemove(this.dynamicTagsR);  /*去重*/
          this.dynamicTagsR = tem_dynamicTagsR;
          this.handleInputConfirmR();
        }
      },
      /*点击建议栏处建议自动添加为建议的标签*/
      addAdvice(index){
        let dynamicTagsA = this.dynamicTagsA;
        if(this.advice){
          if(dynamicTagsA.length !==0){
            return
          }else{
            this.dynamicTagsA.push(index);
            this.handleInputConfirmA();
          }
          if(index==='定时随访'){
            this.dialogVisible=true;
            // this.seen2=true;
          }
          if(index==='进一步就诊检查'){
            this.uplevelVisible=false;  //如果要打开添加上级医院对话框，将false改为true即可
          }
        }
