      /*完成阅片*/
      finishDiagnose(){
          let diaStrR = this.dynamicTagsR.toString();  /*将右眼诊断内容由数组形式转化为字符串形式*/
          let diaStrL = this.dynamicTagsL.toString();  /*将左眼诊断内容由数组形式转化为字符串形式*/
          let diagnose_advice = this.dynamicTagsA[0].toString(); /*将诊断建议化为字符串形式*/
          if(diaStrR.length>40||diaStrL.length>40||diagnose_advice.length>40){
            this.$alert('建议和左右眼诊断内容每条不可超过40个字符', '提示', {
              confirmButtonText: '确定',
            });
            return false;
          }

          this.diaStrR = diaStrR;
          this.diaStrL = diaStrL;
          this.submitDiagnose();  /*提交诊断内容*/
          this.eyeSeen = true;
          this.diaSeen = false;
          this.finishSeen = true;
          this.beforeSeen = false;
      },

