      /*获取诊断内容*/
      getDiagnoseResult(){
        let basic_data = {};
        let other_data = {};
        let total_data = {};
        basic_data['patient_id'] = this.patient_info.patient_id;
        basic_data['diagnose_record'] = this.patient_info.diagnose_record;
        basic_data['apply_time'] = this.patient_info.apply_time;
        basic_data['patient_name'] = this.patient_info.patient_name;
        basic_data['patient_gender'] = this.patient_info.patient_gender;
        basic_data['patient_age'] = this.patient_info.patient_age;
        basic_data['left_diagnose'] = this.diaStrL;
        basic_data['right_diagnose'] = this.diaStrR;
        basic_data['diagnose_advice'] = this.dynamicTagsA[0];
        basic_data['jwsInfo'] = this.jwsDisplayInfo;
        other_data['username'] = cookie.getCookie('name');
        other_data['right_origin_src'] = this.right_src.split('media')[this.right_src.split('media').length-1];
        other_data['left_origin_src'] = this.left_src.split('media')[this.left_src.split('media').length-1];
        //如果右眼这张照片不是noimg
        if(other_data['right_origin_src'].search('noimg')===-1){

          let index= other_data['right_origin_src'].lastIndexOf(".");
          let format = other_data['right_origin_src'].substr(index);

          console.log('新式眼底格式2',format);

          other_data['right_AI_src'] = other_data['right_origin_src'].replace(format,'_AI'+format);
        }else{other_data['right_AI_src'] = this.right_src.split('media')[this.right_src.split('media').length-1];}
        //如果左眼这张照片不是noimg
        if(other_data['left_origin_src'].search('noimg')===-1){

          let index= other_data['left_origin_src'].lastIndexOf(".");
          let format = other_data['left_origin_src'].substr(index);

          console.log('新式眼底格式1',format);

          other_data['left_AI_src'] = other_data['left_origin_src'].replace(format,'_AI'+format);
        }else{
          other_data['left_AI_src'] = this.left_src.split('media')[this.left_src.split('media').length-1];
        }
        if(this.value2){
          basic_data['review_time'] = this.value2;
          basic_data['transform'] = '1'
        }
        if(this.uplevelHospital){
          basic_data['review_hospital'] = this.uplevelHospital;
          basic_data['transform'] = '1'
        }
        other_data['oozed_amount'] = this.AIdata.oozed_amount;
        other_data['bleeding_amount'] = this.AIdata.bleeding_amount;
        other_data['oozed_area'] = this.AIdata.oozed_area;
        other_data['bleeding_area'] = this.AIdata.bleeding_area;
        other_data['max_oozed_area'] = this.AIdata.max_oozed_area;
        other_data['max_bleeding_area'] = this.AIdata.max_bleeding_area;
        other_data['other_advice'] = '';
        total_data['basic_data'] = basic_data;
        total_data['other_data'] = other_data;

        return total_data
      },
