<template>
  <div>
    <div class="c-box bg-box" id="login">
      <div class="login-box clearfix"style="margin-top:10px">
        <div class="fr form-box" v-bind:class="{ register_form_box: isActive }">

              <el-header style="margin-bottom: 10px;margin-top: 20px"><img id="logoImg" style="height: 50px;width: 194px" src="../../static/images/login/LOGO.png" alt="">
              </el-header>
          <template style="margin-left: 10px">
            <el-tabs v-model="activeName" @tab-click="handleClick">
              <el-tab-pane label="立即登录" name="first"></el-tab-pane>
              <el-tab-pane label="立即注册" name="second"></el-tab-pane>
              <el-tab-pane label="忘记密码" name="third"></el-tab-pane>
            </el-tabs>
          </template>

          <el-form id="jsLoginForm" autocomplete="on">

            <input type="hidden" name="csrfmiddlewaretoken" value="ywSlOHdiGsK6VFB6iyhnB1B30khmz8SU">

            <div class="form-group marb20" v-if="generalSeen">
              <label>用&nbsp;户&nbsp;名</label>
              <input name="account_l" id="account_l" type="text" v-model="userName" oninput="value=value.replace(/[\W]/g,'')" maxlength="10"  @focus="errorUnshow" placeholder="支持数字和字母(4-10位)">
            </div>
            <p class="error-text" v-show="userNameError">{{userNameError}}</p>
            <div class="form-group marb8" v-if="generalSeen">
              <label>密&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码</label>
              <input name="password_l" id="password_l" type="password" v-model="parseWord" @focus="errorUnshow" maxlength="10" placeholder="请输入您的密码(8-10位)">
            </div>

            <div class="form-group marb8" v-if="registerSeen">
              <label>确认密码</label>
              <input  name="password_2" id="password_2" type="password" v-model="parseWord2" @focus="errorUnshow" maxlength="10" placeholder="请确认您的密码">
            </div>

            <div class="form-group marb8" v-if="registerSeen">
              <label>邮&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;箱</label>
              <input  name="email" id="email" type="email" v-model="email" @focus="errorUnshow" placeholder="请输入您的邮箱">
            </div>

            <div class="form-group marb8" v-if="forgetSeen">
              <label>邮&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;箱</label>
              <input  name="email" id="reset_email" type="email" v-model="reset_email" @focus="errorUnshow" placeholder="请输入您的注册邮箱">
            </div>

            <div style="text-align: left" v-if="loginSeen">
              <el-checkbox v-model="checked">记住密码</el-checkbox>
            </div>
            <p class="error-text" v-show="parseWordError">{{parseWordError}}</p>
            <!--        <div class="error btns login-form-tips" id="jsLoginTips" v-show="error"><p>用户名或密码错误</p></div> -->
            <div class="auto-box marb38">
              <p class="error-text" v-show="error">{{error}}</p>
            </div>

            <input class="btn btn-green" id="jsLoginBtn" style="outline: none" type="button" @click = "login" value="立即登录 &gt; " v-if="loginSeen">
            <input class="btn btn-green" id="jsRegisterBtn" style="outline: none" type="button" @click = "register" value="立即注册 &gt; " v-if="registerSeen">
            <input class="btn btn-green" id="jsForgetPwdBtn" style="outline: none" type="button" @click = "forgetPwd"
                   v-bind:value=this.forgetPwdValue :disabled="isAble" v-if="forgetSeen">
          </el-form>
<!--          <ul class="form other-form">-->
<!--            <li>-->
<!--              <h5>使用第三方帐号登录</h5>-->
<!--            </li>-->
<!--            <li class="other-login">-->
<!--              <a class="qq" href="http://shop.projectsedu.com:8001/login/qq/"></a>-->
<!--              <a class="sina" href="http://shop.projectsedu.com:8001/login/weibo/"></a>-->
<!--              <a class="weixin" href="http://shop.projectsedu.com:8001/login/weixin/"></a>-->
<!--            </li>-->
<!--          </ul>-->
<!--          <span class="form-p" v-if="loginSeen">-->
<!--            没有帐号？-->
<!--            <a href="javascript:;" @click="clickRegisterHref">[立即注册]</a>-->
<!--            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
<!--            <a href="javascript:;">忘记密码</a>-->
<!--          </span>-->

        </div>

      </div>
    </div>
  </div>
</template>
<script>
  import cookie from '../../static/js/cookie';
  import store from '@/vuex/store'
  import axios from 'axios'
  import url from "../../static/js/hostUrl";
  import { login ,register, forgetPwd} from '../../api/api'
  let host = url.url;
  export default {
    data(){
      return {
        userName:'',
        parseWord:'',
        parseWord2:'',
        autoLogin:false,
        error:false,
        userNameError:'',
        parseWordError:'',
        hospital_id:0,
        checked: true,
        registerSeen:false,
        forgetSeen:false,
        loginSeen:true,
        generalSeen:true,
        activeName: 'first',
        isActive:false,
        email:'',
        reset_email:'',
        forgetPwdValue:'立即发送 >',
        isAble:false
      }
    },
    created:function(){
        document.onkeydown=function(evt){
        evt = (evt) ? evt : ((window.event) ? window.event : "");
          //兼容IE和Firefox获得keyBoardEvent对象
          var key = evt.keyCode?evt.keyCode:evt.which;
         //兼容IE和Firefox获得keyBoardEvent对象的键值
          if(key == 13)
        {
          document.getElementById("jsLoginBtn").click();
          event.returnValue = false;
        }
      },
          this.checked = this.$store.state.checked;
      this.userName=cookie.getCookie('loginName');
      this.parseWord =cookie.getCookie('loginPwd');
    },
    mounted:function() {
      let loginHeight = window.innerHeight;
      document.getElementById('login').style.height = loginHeight+'px';

    },

    methods:{
      get_hospital_id:function (username) {
        axios.get(host+"/users/?username="+username)
          .then(response=>{
            let hospital_id = response.data[0]['hospital_id'];
            console.log('获得了',hospital_id);
            cookie.setCookie('hospital_id',response.data[0]['hospital_id'],7);
            this.hospital_id = hospital_id;
          })
          .catch(error=>{
            console.log((error))
          })
      },

      handleClick() {
       let name = this.activeName;
       this.error = false;
       if(name === 'first'){
         this.loginSeen = true;
         this.registerSeen = false;
         this.generalSeen = true;
         this.isActive = false;
         this.forgetSeen = false;
       }else if(name === 'second'){
         this.loginSeen = false;
         this.forgetSeen = false;
         this.generalSeen = true;
         this.registerSeen = true;
         this.isActive = true;
       }else{
         this.forgetSeen = true;
         this.loginSeen = false;
         this.registerSeen = false;
         this.isActive = false;
         this.generalSeen = false;
       }
      },

      login(){
        // if(this.userName==''||this.parseWord==''){
        //   this.error = true;
        //   return
        // }
        let that = this;
        if(this.userName===''){
          that.error='请输入用户名';
          return;
        }
        if(this.parseWord===''){
          that.error='请输入密码';
          return;
        }
        try {
          this.get_hospital_id(this.userName);
        }catch (e) {
          console.log('the reason of failing to get hospital_id ',e);
          return;
        }
        login({
          username:this.userName, //当前页码
          password:this.parseWord,
        }).then((response)=> {
          console.log(response);
          //本地存储用户信息
          cookie.setCookie('name',this.userName,7);
          cookie.setCookie('token',response.token,7);
          cookie.setCookie('sender','browser',7);
          if(this.checked){
            cookie.setCookie('loginPwd',this.parseWord);
            cookie.setCookie('loginName',this.userName);
          }else{
            cookie.delCookie('loginPwd');
            cookie.delCookie('loginName');
          }
          //存储在store
          // 更新store数据

          that.$store.commit('setInfo',this.checked); // 将用户的name和token保存到vuex中
          that.$store.commit('login');  //隐藏登陆界面 展示首页

        })
          .catch(function (error) {
            that.error = '请核实账户或密码是否正确';
            if("non_field_errors" in error){
              that.error = error.non_field_errors[0];
            }
            if("username" in error){
              that.userNameError = error.username[0];
            }
            if("password" in error){
              that.parseWordError = error.password[0];
            }
          });
      },

      register:function(){
        let username = this.userName;
        let pwd1 = this.parseWord;
        let pwd2 = this.parseWord2;
        let email = this.email;
        let reg = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
        let eamilReg = /^([\.a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;

        if(!username){
          this.error = '用户名不能为空!';
          return false;
        }else if(username.length<4){
          this.error = '用户名长度需4-10位';
          return false;
        }
        else if(!pwd1||!pwd2){
          this.error = '请输入密码';
          return false;
        }else if(pwd1.length<8){
          this.error = '密码长度至少8位';
          return false;
        }else if(!email){
          this.error = '请输入邮箱';
          return false;
        }else if(!eamilReg.test(email)){
          this.error = '邮箱格式不正确';
          return false;
        }else if(pwd1!==pwd2){
          this.error = '两次密码不一致!';
          return  false;
        }

        if(!reg.test(username)){
          this.error = '用户名禁止输入空格等特殊字符!';
          return false;
        }else if(!reg.test(pwd1)){
          this.error = '密码框禁止输入空格等特殊字符';
          return false;
        }else if(!reg.test(pwd2)){
          this.error = '密码框禁止输入空格等特殊字符';
          return false;
        }

        register({
          'username':username,
          'email':email,
          'password1':pwd1,
          'password2':pwd2
        }).then((response)=> {
          this.error = response['info'];
        })
          .catch(function (error) {
          });
      },
      errorUnshow(){
        this.error = false;
      },

      forgetPwd:function () {
        let reset_email = this.reset_email;
        let serverUrl = window.location.href;
        console.log(serverUrl);
        let realUrl = serverUrl.split("templates")[0];
        let eamilReg = /^([\.a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
        if(!reset_email){
          this.error = '请输入邮箱';
          return false;
        }else if(!eamilReg.test(reset_email)){
          this.error = '邮箱格式不正确';
          return false;
        }

        this.sendCode();

        forgetPwd({
          'email':reset_email,
          'url': realUrl,
        }).then((response)=> {
         if(response===2){
           this.error = '发送成功,请登陆邮箱修改密码';
         }else if(response===0){
           this.error = '该邮箱未被注册,请核实';
         }else{
           this.error = '发送失败,请联系管理员';
         }
        })
          .catch(function (error) {
          });
      },


      sendCode:function () {
    let timer_num = 60;
    this.isAble = true;
    let timeClock=setInterval(()=>{
      timer_num--;
     console.log(timer_num);
     this.forgetPwdValue = timer_num+'秒后可重新发送';
      if (timer_num === 0) {
        clearInterval(timeClock);
        this.isAble = false;
        this.forgetPwdValue = '立即发送 >'
      }
    },1000);

  }

  },


  }
</script>
<style  scoped>
  .error-text{
    color:#fa8341;
  }
  .other-form li {
    padding-bottom: 8px;
    margin-bottom: 10px;
  }
  .other-form li h5 {
    font-size:12px;
  }

  .other-login a {
    margin-top: 0;
    vertical-align: top;
    margin-right: 10px;
    background: url(../../static/images/login/other-login-bg.png) center no-repeat;
    display: inline-block;
    width: 30px;
    height: 30px;
    overflow: hidden;
  }
  .other-login a.qq {
    background-position: -40px 0;
  }
  .other-login a.sina {
    background-position: 0 0;
  }
  .other-login a.alipay {
    background-position: -80px 0;
  }
  .other-login a.weixin {
    background-position: -200px 0;
  }
  .c-box{
    width:100%;
    min-width: 1190px;
    overflow:hidden;
  }
  .bg-box{
    background:url(../../static/images/login/loginBg1.jpg) no-repeat center center;

    background-size:cover;

    background-attachment:fixed;

    background-color:#CCCCCC;
  }
  .login-box{
    width:853px;
    margin:0px auto;
  }
  .clearfix::after {
    clear: both;
    content: " ";
    display: block;
    font-size: 0;
    height: 0;
    visibility: hidden;
  }

  .hd-login{
    position:relative;
    height:68px;
    line-height:68px;
    margin-bottom:15px;
    padding-top:32px;
    padding-left:190px;
    /*  background:url(../../static/images/login/logo.png) no-repeat 0 center;*/
  }
  .index-logo{
    position:absolute;
    left:0;
    top:0;
    display:block;
    width:190px;
    height:100px;
    cursor: pointer;
  }
  .index-font{
    float:right;
    height:20px;
    line-height:20px;
    margin-top:48px;
    padding-left:20px;
    color:#fff;
    /*background:url(../../static/images/login/homepage.png) no-repeat 0 top;*/
  }
  .fl{float:left!important;}
  .fr{float:right!important;}
  .slide{
    position: relative;
    width: 483px;
    height: 472px;
    background:#fff;
    overflow: hidden;
  }
  .imgslide {
    width:100%;
    height:100%;
  }
  .imgslide li {
    float:left;
  }
  .imgslide .dots{
    position:absolute;
    bottom:10px;
    left:50%;
    width:80px;
    margin-left:-40px;
  }
  .imgslide .dots li{
    float:left;
    margin:5px;
    background:#fff;
    width:12px;
    height:12px;
    border-radius:50%;
    color:#fff;
    text-align:center;
    cursor:pointer;
    overflow: hidden;
  }
  .imgslide .dots li.active{
    background:#6ec559;
    color:#6ec559;
  }
  .unslider-arrow {
    position: absolute;
    width:33px;
    height:50px;
    top: 45%;
    cursor: pointer;
    z-index:100;
  }
  .unslider-arrow.prev {
    left: 0;
    background:url(../../static/images/login/slide_l.png) no-repeat center center;
  }
  .unslider-arrow.prev:hover {
    background:url(../../static/images/login/slide_l_1.png) no-repeat center center;
  }
  .unslider-arrow.next{
    right:0;
    background:url(../../static/images/login/slide_r.png) no-repeat center center;
  }
  .unslider-arrow.next:hover{
    background:url(../../static/images/login/slide_r_1.png) no-repeat center center;
  }
  .hd-login > h1{
    float:left;
    color:#fff;
    font-size:30px;
    font-weight: normal;
  }



  .form-box .form-group.focus,
  .form-box .valcode.error {
    border-color: #6ec558;
    box-shadow: 0 0 5px #6ec558;
  }
  .form-box .form-group.blur,
  .form-box  .valcode.blur {
    border-color: #ccc;
  }
  .form-box .form-group.errorput,
  .form-box .valcode.errorput input {
    border-color: #f00;
    box-shadow: 0 0 5px #aa0b0b;
  }
  .form-box a{
    color:#666;
  }
  .form-box a:hover{
    color:#6ec559;
  }
  .form-box{
    position:fixed;
    margin-left: 35%;
    margin-top: 10%;
    width:290px;
    height: 420px;
    padding:0 40px;
    background:#fff;
    color:#666;
    box-shadow: 0 0 10px lightgrey;
  }

  .register_form_box{
    position:fixed;
    margin-left: 35%;
    margin-top: 10%;
    width:290px;
    height: 480px;
    padding:0 40px;
    background:#fff;
    color:#666;
    box-shadow: 0 0 10px lightgrey;
  }

  .form-box > h2,
  .form-box > .tab{
    height:54px;
    line-height:54px;
    margin-bottom:34px;
    font-size:18px;
    font-weight:normal;
    color:#333;
    border-bottom:1px solid #eaeaea;
  }
  .form-box > .tab > h2{
    float:left;
    width:90px;
    height:53px;
    line-height:53px;
    cursor: pointer;
    font-weight:normal;
    text-align:center;
  }
  .form-box > .tab > h2.active{
    border-bottom:3px solid #6ec55a;
    color:#333;
  }


  .form-group{
    position:relative;
    width:288px;
    height:38px;
    border:1px solid #dedede;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    overflow:hidden;
  }
  .form-group > label{
    float:left;
    width:72px;
    height:20px;
    line-height:20px;
    margin-top:9px;
    border-right:1px solid #eaeaea;
    text-align:center;
  }
  .form-group > input{
    float:left;
    width:195px;
    line-height:24px;
    padding:7px 10px;
    border:0;
    line-height:normal\9;
    padding:12px 10px 9px\9;
  }
  .form-group .mobile-register-captcha{
    width:85px;
  }
  .form-group .captcha{
    cursor: pointer;
  }
  .marb20{
    margin-bottom:8px;
  }
  .marb8{
    margin-bottom:8px;
    margin-top: 20px;
  }
  .marb38{
    margin-bottom:38px;
  }
  .error{
    background: #fb8344;
    color:#fff !important;
    text-align: center;
    height:40px !important;
    line-height: 40px !important;
    margin:10px 0;
  }
  .auto-box{
    height:18px;
    line-height:18px;
  }
  .auto-box > label > input{
    vertical-align: sub;
  }
  .auto-box > label > a{
    color:#6ec559;
  }
  .btn{
    width:100%;
    height:42px;
    line-height:42px;
    font-size:14px;
    color:#fff;
    border:0;
    cursor:pointer;
  }
  .btn-green{
    background:#20A2EA;
  }
  .btn-green:hover{
    background:#5dbf45;
  }
  .form-p{
    position:absolute;
    left:40px;
    bottom:25px;
  }
  .form-p > a{
    color:#fa8341;
  }
  .form-p > a:hover{
    color:#666;
  }

  a{
    text-decoration: none;
    color: #2F4050;
  }

  .form-box >>>.el-tabs__nav-scroll{
    padding-left: 15px;
  }


</style>



