<template>
  <div class="leftNav" id="leftNav">
    <el-container>
      <el-header class="logo"><img id="logoImg" src="../../static/images/logo/orbislogo.png" alt="">
        OrbisBioTech
      </el-header>
    </el-container>
    <el-container>
      <el-menu
        default-active="1-1"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#2F4050"
        text-color="#fff">
        <el-submenu index="1">
          <template slot="title">
            <i class="icon iconfont icon-yiliao_"></i>
            <span>眼底诊断系统(V2.0)</span>
          </template>

          <el-menu-item id="undiagnosed" index="1-1" @click="$store.commit('clickUndiagnosed')">未诊列表</el-menu-item>
          <el-menu-item id="diagnosed" index="1-2" @click="$store.commit('clickDiagnosed')">已诊列表</el-menu-item>

        </el-submenu>

        <el-menu-item index="3" @click="$store.commit('clickTransform')">
          <i class="icon iconfont icon-zhuanhuan"></i>
          <span slot="title">病患转化</span>
        </el-menu-item>
        <el-menu-item index="5" @click="$store.commit('clickStatistics')">
          <i class="icon iconfont icon-shuju1"></i>
          <span slot="title">数据统计</span>
        </el-menu-item>
      </el-menu>
    </el-container>
    <el-container>
      <el-footer><el-popover
        placement="top-start"
        title="关于我们"
        width="400"
        trigger="hover"
        >
        <el-row>
          <el-col :span="24"><i class="el-icon-office-building"></i>&nbsp;&nbsp;公司:合肥奥比斯科技有限公司</el-col>
          <el-col :span="24"><i class="el-icon-message"></i>&nbsp;&nbsp;邮箱:support@orbisbiotech.com</el-col>
          <el-col :span="24"><i class="el-icon-phone"></i>&nbsp;&nbsp;电话:0551-63826232</el-col>
          <el-col :span="24"><i class="el-icon-location"></i>&nbsp;&nbsp;地址:合肥市高新区黄山路605号</el-col>
          <el-col :span="24"><i class="el-icon-info"></i>&nbsp;&nbsp;版本:眼底诊断系统V2.0</el-col>
        </el-row>
        <el-button class="about" slot="reference" icon="el-icon-info">关于我们</el-button>
      </el-popover></el-footer>
    </el-container>
  </div>
</template>

<script>
  import store from '@/vuex/store'
  export default {
    name: "orbis_leftNav",
    store,
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      add(){
          console.log('add Event from leftNav');
          store.commit('increase')
      }
    },
    mounted:function() {
      let leftNavHeight = window.innerHeight;
      console.log(leftNavHeight);
      document.getElementById('leftNav').style.height = leftNavHeight+'px';
    },
  }
</script>

<style scoped>
  .logo{
    height: 60px;
    background-color: #1D2D3B;
    text-align: center;
    color: white;
    line-height: 60px;
    padding: 0px;
    margin: 0px;
    overflow: hidden;
    font-weight: 700;
  }

  .el-menu-vertical-demo{
    width: 100%;
    text-align: left;
  }
  .leftNav{
    width:230px;
    background-color: #2F4050;
    float: left;
  }
  .el-menu{
    border-right: none;
  }
  #diagnosed,#undiagnosed{
    width: 10%;
  }
  span{
    padding-left: 10px;
  }
  #logoImg{
    height: 50px;
    padding-top: 5px;
  }

  .el-footer {
    position: absolute;
    bottom: 0;
    line-height: 80px;

    text-align: center;

  }

  .about{
   background-color: transparent;
    border: none;
    color: lightgrey;
    font-size: 12px;
  }


</style>




