import axios from 'axios';
import service from '../axios/request'
import url from "../static/js/hostUrl";
let base = url.url;

export const login = params => { return axios.post(`${base}login`, params).then(res => res.data); };

export const register = params => { return axios.post(`${base}logoutOperation/register/`, params).then(res => res.data); };

export const forgetPwd = params => { return axios.post(`${base}logoutOperation/forgetPwd/`, params).then(res => res.data); };

export const checkPatients = params => { return axios.post(`${base}operation/check/`, params).then(res => res.data); };

export const removePatients = params => { return axios.get(`${base}/operation/remove/`, { params: params }); };

export const batchRemoveUser = params => { return axios.get(`${base}/user/batchremove`, { params: params }); };

export const editPatientBasicInfos = params => { return axios.post(`${base}operation/editBasicInfo/`, params).then(res => res.data); };

export const editPatientReviewInfos = params => { return axios.post(`${base}operation/editReviewInfo/`, params).then(res => res.data); };

export const addPatients = params => { return axios.post(`${base}operation/add/`, params).then(res => res.data); };

export const sendSmsToPatient = params => { return axios.post(`${base}operation/sendSms/`, params).then(res => res.data); };

export const checkPatient = data => {
  return service({
    url: 'operation/check/',
    method: 'post',
    data
  })
};

export const removePatient = data => {
  return service({
    url: 'operation/remove/',
    method: 'post',
    data
  })
};

export const editPatientBasicInfo = data => {
  return service({
    url: 'operation/editBasicInfo/',
    method: 'post',
    data
  })
};

export const editPatientReviewInfo = data => {
  return service({
    url: 'operation/editReviewInfo/',
    method: 'post',
    data
  })
};

export const addPatient = data => {
  return service({
    url: 'operation/add/',
    method: 'post',
    data
  })
};

export const clickDiagnoseButton = data => {
  return service({
    url: 'operation/clickDiagnoseButton/',
    method: 'post',
    data
  })
};

export const get_patient_info = data => {
  return service({
    url: 'patients/',
    method: 'get',
    params:data
  })
};

export const user_diagnose = data => {
  return service({
    url: 'operation/submit/',
    method: 'post',
    data
  })
};

export const check_expired = data => {
  return service({
    url: 'operation/check_expired/',
    method: 'post',
    data
  })
};

export const createImg = data => {
  return service({
    url: 'operation/createImg/',
    method: 'post',
    data
  })
};



